.coblocks-layout-selector-modal .components-modal__header,
.coblocks-layout-selector-modal .coblocks-layout-selector__sidebar {
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
}
.coblocks-layout-selector-modal .components-modal__header-heading {
  font-size: 1.5em !important;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__layout::after {
  border-color: #1e1e1e;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__about {
  font-size: 1.15em;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__content {
  padding-top: 0;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__sidebar__items {
  margin-top: 2em;
}
.coblocks-layout-selector-modal .components-modal__header .components-modal__header-heading-container .components-modal__header-heading > span {
  left: calc(135px + 5%) !important;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__add-button {
  font-size: 1.1em;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__add-button > span {
  position: relative;
  top: -1px;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__dropdown {
  display: flex;
  max-width: var(--nextgen-popover-content-width);
}
.coblocks-layout-selector-modal .coblocks-layout-selector__dropdown-button.is-link {
  border: 1px solid transparent;
  color: var(--wp-admin-theme-color);
  display: flex;
  font-size: 1.25em;
  margin: 5px 0;
  padding-left: 0;
  text-decoration: none;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__dropdown-button.is-link:hover {
  color: var(--wp-admin-theme-color-darker-10) !important;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__dropdown-button.is-link svg {
  transition: 100ms transform linear;
}
.coblocks-layout-selector-modal .coblocks-layout-selector__dropdown-button.is-link.is-open svg {
  transform: rotate(-180deg);
}
.coblocks-layout-selector-modal .coblocks-layout-selector__category-slug {
  color: var(--wp-admin-theme-color);
  display: flex;
  font-size: 1.25em;
  margin: 5px 0 7px;
  padding-left: 0;
  text-decoration: none;
}

.coblocks-layout-selector__pop {
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
}
.coblocks-layout-selector__pop .components-popover__content {
  border-color: #1e1e1e;
  box-shadow: none;
  margin-left: -100px !important;
  margin-top: -7px !important;
  max-width: var(--nextgen-popover-content-width) !important;
  min-width: var(--nextgen-popover-content-width) !important;
  overflow-x: hidden;
}
.coblocks-layout-selector__pop .components-popover__content > div {
  padding: 0 !important;
}
.coblocks-layout-selector__pop .components-popover__content > div .coblocks-layout-selector__category-search {
  display: flex;
  width: var(--nextgen-popover-content-width);
}
.coblocks-layout-selector__pop .components-popover__content > div .coblocks-layout-selector__category-search-input {
  border-radius: 0;
  border-right: none;
  font-size: 15px;
  margin: 0;
  margin: -1px -1px 0 -1px;
  min-height: 40px;
  padding-bottom: 5px;
  padding-left: 14px;
  padding-right: 30px;
  padding-top: 5px;
  width: calc(var(--nextgen-popover-content-width) + 2px);
}
.coblocks-layout-selector__pop .components-popover__content > div .coblocks-layout-selector__category-search-input:focus {
  border-color: #1e1e1e;
  box-shadow: none;
  outline: none;
}
.coblocks-layout-selector__pop .components-popover__content > div .coblocks-layout-selector__category-search-icon {
  align-items: center;
  display: flex;
  margin: 9px 3px 3px -34px;
}
.coblocks-layout-selector__pop .components-popover__content > div .image-category__suggestions-list {
  list-style: none;
  margin: 0;
  max-height: 25em;
  overflow-y: auto;
  padding-top: 0;
  transition: all 0.15s ease-in-out;
  width: var(--nextgen-popover-content-width);
}
.coblocks-layout-selector__pop .components-popover__content > div .image-category__suggestion {
  border-radius: 0;
  color: #1e1e1e;
  cursor: pointer;
  display: block;
  font-size: 15px;
  height: auto;
  margin: 0;
  padding: 12px 13px;
  text-align: left;
}
.coblocks-layout-selector__pop .components-popover__content > div .image-category__suggestion.is-selected {
  background: var(--wp-admin-theme-color);
  color: #fff;
}
.coblocks-layout-selector__pop .components-popover__content > div .image-category__suggestion:focus {
  box-shadow: none;
}
.coblocks-layout-selector__pop .components-popover__content > div .image-category__suggestion-match {
  text-decoration: underline;
}